import { Box, Typography } from "@mui/material";
import React from "react";
import newWay from "../Assets/newWay.jpg";

const Newskill = () => {
  return (
    <>
      <Box>
        <Box
          sx={{
            height: "100vh",
            width: "80vw",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <Box
            sx={{
              p: "2rem",
              borderRadius: "2rem",
              background: "rgba(255, 255, 255, 0.25)",
              boxShadow: "0 8px 32px 0 rgba(31, 38, 135, 0.37)",
              backdropFilter: "blur(4px)",
              WebkitBackdropFilter: "blur(4px)",
              border: "1px solid rgba(255, 255, 255, 0.18)",
              animation: "rgbaShadow 5s ease-in-out infinite",
            }}
          >
            <Typography
              className="text-head"
              variant="body1"
              sx={{
                fontSize: "2.5rem ",
                fontFamily: "Londrina Sketch",
                fontWeight: 900,
              }}
            >
              A new way of teaching :
            </Typography>
            <Box>
              <Typography variant="body1" sx={{ fontSize: "0.9rem" }}>
                Now day's school use some ways of teaching
              </Typography>
              <Typography variant="body1" sx={{ fontSize: "0.9rem" }}>
                Here are some way that school use in there curriculam.
              </Typography>
              <Box sx={{ display: "flex", gap: "1rem" }}>
                <ul style={{ paddingLeft: "1rem", width: "60%" }}>
                  <li>
                    <Typography
                      className="text-head"
                      variant="body1"
                      sx={{
                        fontSize: "0.8rem ",
                      }}
                    >
                      <span style={{ color: "#FF4C7A", fontWeight: "700" }}>
                        Blended Learning :
                      </span>{" "}
                      Combine traditional face to face instruction this allow
                      With access online components. materials and Complete
                      assignment at ther pair whill intractions I benefiting
                      from own in Pursab.
                    </Typography>
                  </li>
                  <li>
                    <Typography
                      className="text-head"
                      variant="body1"
                      sx={{
                        fontSize: "0.8rem ",
                      }}
                    >
                      <span style={{ color: "#3D7BE4", fontWeight: "700" }}>
                        Gamification:
                      </span>{" "}
                      Using games like element in education to make learning
                      more engaging. Student can earn points or rewards as they
                      progress thought lesson or master new concepts, make
                      education interaction and fun.
                    </Typography>
                  </li>
                  <li>
                    <Typography
                      className="text-head"
                      variant="body1"
                      sx={{
                        fontSize: "0.8rem ",
                      }}
                    >
                      <span style={{ color: "#FF8A66", fontWeight: "700" }}>
                        Flipped Classroom:
                      </span>{" "}
                      Student review instructional content at home
                      (video,reading etc.) and use class time for hands on
                      activities and personalized guidance from the teacher.
                    </Typography>
                  </li>
                  <li>
                    <Typography
                      className="text-head"
                      variant="body1"
                      sx={{
                        fontSize: "0.8rem ",
                      }}
                    >
                      <span style={{ color: "#C04CFF", fontWeight: "700" }}>
                        Artificial Intelligence (AI) in education:
                      </span>{' '}
                      All drivin tools can access students progress, provide
                      instant feedback and often personalized learning path
                      based on individual needs.
                    </Typography>
                  </li>
                  <li>
                    <Typography
                      className="text-head"
                      variant="body1"
                      sx={{
                        fontSize: "0.8rem ",
                      }}
                    >
                      <span style={{ color: "#FF89A3", fontWeight: "700" }}>
                        Social learning:
                      </span>{" "}
                      Encourages collabaration and peer to peer intraction
                      thought can share ideas, work on group project, and
                      provide feedback to one another.
                    </Typography>
                  </li>
                </ul>
                <Box
                  sx={{
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                  }}
                >
                  <Box
                    sx={{
                      height: "14rem",
                      borderRadius: "1rem",
                      overflow: "hidden",
                    }}
                  >
                    <img
                      src={newWay}
                      alt="No Preview"
                      style={{
                        height: "100%",
                        width: "100%",
                        objectFit: "contain",
                        mixBlendMode: "multiply",
                      }}
                      onError={(e) => {
                        e.onerror = null;
                        e.target.src = require("../Assets/images.jpeg");
                      }}
                    />
                  </Box>
                </Box>
              </Box>
            </Box>
          </Box>
        </Box>
      </Box>
    </>
  );
};

export default Newskill;
