import { Box, Typography } from "@mui/material";
import React from "react";
import Learn from "../Assets/learn.jpg";

const Conclusion = () => {
  return (
    <>
      <Box>
        <Box
          sx={{
            height: "100vh",
            width: "80vw",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            flexDirection: "column",
            gap: "1rem",
          }}
        >
          <Box
            sx={{
              p: "1rem",
              borderRadius: "2rem",
              background: "rgba(255, 255, 255, 0.25)",
              boxShadow: "0 8px 32px 0 rgba(31, 38, 135, 0.37)",
              backdropFilter: "blur(4px)",
              WebkitBackdropFilter: "blur(4px)",
              border: "1px solid rgba(255, 255, 255, 0.18)",
              animation: "rgbaShadow 5s ease-in-out infinite",
            }}
          >
            <Typography
              className="text-head"
              variant="body1"
              sx={{
                fontSize: "3rem ",
                fontFamily: "Londrina Sketch",
                fontWeight: 900,
              }}
            >
              Conclusion :
            </Typography>
            <Box sx={{ display: "flex", gap: "1rem" }}>
              <Box
                sx={{
                  width: "50%",
                  display: "flex",
                  flexDirection: "column",
                  gap: "0.7rem",
                }}
              >
                <Typography
                  className="text-head"
                  variant="body1"
                  sx={{
                    fontSize: "0.9rem ",
                  }}
                >
                  In conclusion, while school English curricula offer a solid
                  foundation in language skills, they must adapt to meet the
                  needs of today’s job market. Beyond literature and grammar,
                  students need practical skills like effective communication,
                  critical thinking, and digital literacy.
                </Typography>
                <Typography
                  className="text-head"
                  variant="body1"
                  sx={{
                    fontSize: "0.9rem ",
                  }}
                >
                  For example, careers in digital marketing, content creation,
                  and management require strong writing, speaking, and
                  collaborative abilities across digital platforms. Schools
                  should focus on teaching real-world applications like
                  professional writing and presentations to better prepare
                  students for modern careers, ensuring they are equipped to
                  succeed in a global and competitive workforce.
                </Typography>
              </Box>
              <Box
                sx={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  width: "50%",
                }}
              >
                <Box
                  sx={{
                    height: "15rem",
                    borderRadius: "1rem",
                    overflow: "hidden",
                  }}
                >
                  <img
                    src={Learn}
                    alt="No Preview"
                    style={{
                      height: "100%",
                      width: "100%",
                      objectFit: "contain",
                      mixBlendMode: "multiply",
                    }}
                    onError={(e) => {
                      e.onerror = null;
                      e.target.src = require("../Assets/images.jpeg");
                    }}
                  />
                </Box>
              </Box>
            </Box>
          </Box>
        </Box>
      </Box>
    </>
  );
};

export default Conclusion;
