import { Box, Typography } from "@mui/material";
import React from "react";
import skill from "../Assets/skillNeed.avif";

const WorkPlace = () => {
  return (
    <>
      <Box>
        <Box
          sx={{
            height: "100vh",
            width: "80vw",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <Box
            sx={{
              p: "2rem",
              borderRadius: "2rem",
              background: "rgba(255, 255, 255, 0.25)",
              boxShadow: "0 8px 32px 0 rgba(31, 38, 135, 0.37)",
              backdropFilter: "blur(4px)",
              WebkitBackdropFilter: "blur(4px)",
              border: "1px solid rgba(255, 255, 255, 0.18)",
              animation: "rgbaShadow 5s ease-in-out infinite",
            }}
          >
            <Typography
              className="text-head"
              variant="body1"
              sx={{
                fontSize: "2.5rem ",
                fontFamily: "Londrina Sketch",
                fontWeight: 900,
              }}
            >
              Skill required at workplace :
            </Typography>
            <Box sx={{ display: "flex", gap: "1rem" }}>
              <Box
                sx={{
                  width: "60%",
                  display: "flex",
                  gap: "0.5rem",
                  flexDirection: "column",
                }}
              >
                <Typography
                  variant="body1"
                  sx={{
                    fontSize: "0.8rem ",
                    fontWeight: 400,
                    fontFamily: "Open Sans, sans-serif",
                  }}
                >
                  To thrive in today's competitive job market, professionals
                  need a combination of technical abilities and soft skills.
                  Here are some of the essential skills required at the
                  workplace:
                </Typography>
                <Typography
                  variant="body1"
                  sx={{
                    fontSize: "0.8rem ",
                    fontWeight: 400,
                    fontFamily: "Open Sans, sans-serif",
                  }}
                >
                  Some skills that are required at the workplace include:
                  <ul style={{ paddingLeft: "1rem" }}>
                    <li>
                      <span style={{ color: "#FF4C7A", fontWeight: "700" }}>
                        Verbal Communication skill :
                      </span>{" "}
                      Being able to communicate verbally, nonverbally, and in
                      writing.
                    </li>
                    <li>
                      {" "}
                      <span style={{ color: "#5C9F3C", fontWeight: "700" }}>
                        Teamwork:
                      </span>{" "}
                      Sharing ideas, listening to others, and working together
                      to solve problems.
                    </li>
                    <li>
                      <span style={{ color: "#F69C29", fontWeight: "700" }}>
                        Problem-solving:
                      </span>{" "}
                      Crafting clear emails, reports, content, or messages for
                      professional and creative purposes.
                    </li>
                    <li>
                      <span style={{ color: "#FF4C7A", fontWeight: "700" }}>
                        Prioritization:
                      </span>{" "}
                      Being able to identify what's most important and focus on
                      it.
                    </li>
                    <li>
                      <span style={{ color: "#5C9F3C", fontWeight: "700" }}>
                        Technology:{" "}
                      </span>
                      Being able to use computers, office equipment, and
                      software.
                    </li>
                    <li>
                      <span style={{ color: "#0080BE", fontWeight: "700" }}>
                        Positive attitude:{" "}
                      </span>
                      Being confident, energetic, and having a sense of humor.
                    </li>
                    <li>
                      <span style={{ color: "#FF8A66", fontWeight: "700" }}>
                        Willingness to learn:{" "}
                      </span>
                      Being open to applying new concepts and technology.
                    </li>
                    <li>
                      <span style={{ color: "#C04CFF", fontWeight: "700" }}>
                        Initiative:{" "}
                      </span>
                      Taking initiative and asking questions for support
                    </li>
                    <li>
                      <span style={{ color: "#70FFAF", fontWeight: "700" }}>
                        Professionalism:{" "}
                      </span>
                      Having a strong work ethic.
                    </li>
                  </ul>
                </Typography>
              </Box>
              <Box
                sx={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                }}
              >
                <Box
                  sx={{
                    height: "14rem",
                    borderRadius: "1rem",
                    overflow: "hidden",
                  }}
                >
                  <img
                    src={skill}
                    alt="No Preview"
                    style={{
                      height: "100%",
                      width: "100%",
                      objectFit: "contain",
                      mixBlendMode: "multiply",
                    }}
                    onError={(e) => {
                      e.onerror = null;
                      e.target.src = require("../Assets/images.jpeg");
                    }}
                  />
                </Box>
              </Box>
            </Box>
          </Box>
        </Box>
      </Box>
    </>
  );
};

export default WorkPlace;
