import React, { useEffect, useRef, useState, useCallback } from "react";
import { gsap } from "gsap";

const PageTransition = ({ color = "#2f3238", totalPages = 5, children }) => {
  const overlayPathRef = useRef(null);
  const contentRef = useRef(null);
  const [isAnimating, setIsAnimating] = useState(false);
  const [page, setPage] = useState(1);
  const [direction, setDirection] = useState("right");
  const [contentVisible, setContentVisible] = useState(true);

  const paths = {
    right: {
      unfilled: "M 0 0 h 0 c 0 50 0 50 0 100 H 0 V 0 Z",
      inBetween: "M 0 0 h 43 c -60 55 140 65 0 100 H 0 V 0 Z",
      filled: "M 0 0 h 100 c 0 50 0 50 0 100 H 0 V 0 Z",
    },
    left: {
      unfilled: "M 100 0 h 0 c 0 50 0 50 0 100 H 100 V 0 Z",
      inBetween: "M 100 0 h -43 c 60 55 -140 65 0 100 H 100 V 0 Z",
      filled: "M 100 0 h -100 c 0 50 0 50 0 100 H 100 V 0 Z",
    },
  };

  const animate = useCallback(
    (newDirection) => {
      if (isAnimating) return;

      const overlayPath = overlayPathRef.current;
      if (!overlayPath) {
        console.error("Overlay path ref is null");
        return;
      }

      const currentPaths = paths[newDirection];

      setIsAnimating(true);
      setDirection(newDirection);

      gsap
        .timeline({
          onComplete: () => {
            setIsAnimating(false);
            setContentVisible(true);
          },
        })
        .to(contentRef.current, {
          duration: 0.3,
          opacity: 0,
          onComplete: () => setContentVisible(false),
        })
        .set(overlayPath, { attr: { d: currentPaths.unfilled } })
        .to(overlayPath, {
          duration: 1,
          ease: "power3.in",
          attr: { d: currentPaths.inBetween },
        })
        .to(overlayPath, {
          duration: 0.2,
          ease: "power4",
          attr: { d: currentPaths.filled },
          onComplete: () =>
            setPage((prevPage) =>
              newDirection === "right"
                ? Math.min(prevPage + 1, totalPages)
                : Math.max(prevPage - 1, 1)
            ),
        })
        .to(overlayPath, {
          duration: 0.3,
          ease: "power4.out",
          attr: { d: currentPaths.unfilled },
        })
        .to(contentRef.current, {
          duration: 0.3,
          opacity: 1,
        });
    },
    [isAnimating, paths, totalPages]
  );

  const handleNavigation = useCallback(
    (newDirection) => {
      if (
        (newDirection === "right" && page < totalPages) ||
        (newDirection === "left" && page > 1)
      ) {
        animate(newDirection);
      }
    },
    [animate, page, totalPages]
  );

  useEffect(() => {
    const nextButton = document.querySelector(".button--next");
    const prevButton = document.querySelector(".button--prev");

    if (!nextButton || !prevButton) {
      console.error("Navigation buttons not found");
      return;
    }

    const handleNext = () => handleNavigation("right");
    const handlePrev = () => handleNavigation("left");

    nextButton.addEventListener("click", handleNext);
    prevButton.addEventListener("click", handlePrev);

    return () => {
      nextButton.removeEventListener("click", handleNext);
      prevButton.removeEventListener("click", handlePrev);
    };
  }, [handleNavigation]);

  const pageContents = React.Children.toArray(children);

  return (
    <div
      className="page-transition"
      style={{
        position: "fixed",
        top: 0,
        left: 0,
        width: "100%",
        height: "100%",
        zIndex: 9999,
        overflow: "hidden",
      }}
    >
      <svg
        className="overlay"
        width="100%"
        height="100%"
        viewBox="0 0 100 100"
        preserveAspectRatio="none"
        style={{ position: "absolute", top: 0, left: 0, pointerEvents: "none" }}
      >
        <path
          ref={overlayPathRef}
          className="overlay__path"
          vectorEffect="non-scaling-stroke"
          d={paths[direction].unfilled}
          fill={color}
        />
      </svg>
      <div
        ref={contentRef}
        className="content"
        style={{
          position: "absolute",
          top: 0,
          left: 0,
          width: "100%",
          height: "100%",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          zIndex: 1,
          opacity: contentVisible ? 1 : 0,
          transition: "opacity 0.3s ease",
        }}
      >
        {pageContents[page - 1] || <div>No content for this page</div>}
      </div>
      <div
        className="navigation"
        style={{
          position: "absolute",
          bottom: "20px",
          left: 0,
          right: 0,
          display: "flex",
          justifyContent: "space-between",
          gap: "20px",
          zIndex: 2,
          padding: "1rem",
        }}
      >
        <button
          className="button button--prev"
          disabled={page === 1 || isAnimating}
        >
          <span>Previous</span>
        </button>

        <button
          className="button button--next"
          disabled={page === totalPages || isAnimating}
        >
          <span>Next</span>
        </button>
      </div>
    </div>
  );
};

export default PageTransition;
