import React from "react";
import ReactApexChart from "react-apexcharts";

const PiChart = () => {
  const languageData = [
    { english: "55.5" },
    { Russian: "5.9" },
    { German: "5.7" },
    { Japanese: "4.9" },
    { spanish: "4.6" },
    { french: "4.6" },
    { other: "7.6" },
  ];

  const series = languageData.map((item) => parseFloat(Object.values(item)[0]));
  const labels = languageData.map((item) => Object.keys(item)[0]);

  const [chartData] = React.useState({
    series: series,
    options: {
      chart: {
        width: 380,
        type: "donut",
      },
      labels: labels.map(
        (label) => label.charAt(0).toUpperCase() + label.slice(1)
      ),
      plotOptions: {
        pie: {
          startAngle: -90,
          endAngle: 270,
        },
      },
      dataLabels: {
        enabled: false,
      },
      fill: {
        type: "gradient",
      },
      legend: {
        formatter: function (val, opts) {
          // Using HTML to style the text after the dash
          return (
            val +
            ' - <span style="color: #fff">' +
            opts.w.globals.series[opts.seriesIndex] +
            "%</span>"
          );
        },
        labels: {
          useSeriesColors: false,
          colors: "#ffff", // Change legend text color here
        },
        markers: {
          width: 12,
          height: 12,
          strokeWidth: 0,
          strokeColor: "#fff",
          radius: 12,
        },
      },

      responsive: [
        {
          breakpoint: 480,
          options: {
            chart: {
              width: 200,
            },
            legend: {
              position: "bottom",
            },
          },
        },
      ],
    },
  });

  return (
    <div className="flex justify-center items-center">
      <div id="chart">
        <ReactApexChart
          options={chartData.options}
          series={chartData.series}
          type="donut"
          width={380}
        />
      </div>
      <div id="html-dist"></div>
    </div>
  );
};

export default PiChart;
