import { colors } from "@mui/material";
import React from "react";
import ReactApexChart from "react-apexcharts";

const DataChart = () => {
  const options = {
    chart: {
      height: 350,
      type: "bar",
      toolbar: {
        show: false,
      },
      animations: {
        enabled: true,
      },
    },
    plotOptions: {
      bar: {
        borderRadius: 10,
        columnWidth: "50%",
        distributed: true, // This helps with label alignment
      },
    },
    dataLabels: {
      enabled: false,
    },
    legend: {
      show: false,
    },
    stroke: {
      width: 0,
    },
    xaxis: {
      labels: {
        rotate: -45,
        rotateAlways: true, // Forces label rotation
        offsetY: 0,
        style: {
          fontSize: "0.8rem",
          colors:"#FF4C7A"
        },
        hideOverlappingLabels: false, // Prevents label hiding
      },
      categories: [
        "English",
        "Chinese",
        "Spanish",
        "Japanese",
        "German",
        "French",
        "Arabic",
        "Italian",
        "Portuguese",
        "Korean",
      ],
      position: "bottom",
      axisBorder: {
        show: true,
      },
      axisTicks: {
        show: true,
      },
      crosshairs: {
        show: false,
      },
      tooltip: {
        enabled: false,
      },
    },
    yaxis: {
      title: {
        text: "Av.Gross Domestic Product 2019 - 2021",
        style: {
          fontSize: "0.7rem",
          color: "#0080BE",
        },
      },
      labels: {
        show: true,
        align: "left",
        style: {
          fontSize: "0.7rem",
          colors: "#FF4C7A",
        },
      },
    },
    grid: {
      xaxis: {
        lines: {
          show: false,
        },
      },
      yaxis: {
        lines: {
          show: true,
        },
      },
    },
    fill: {
      type: "gradient",
      gradient: {
        shade: "light",
        type: "horizontal",
        shadeIntensity: 0.25,
        gradientToColors: undefined,
        inverseColors: true,
        opacityFrom: 0.85,
        opacityTo: 0.85,
        stops: [50, 0, 100],
      },
    },
    colors: ["#008FFB"],
  };

  const series = [
    {
      name: "Av.Gross Domestic Product 2019 - 2021",
      data: [24, 17, 7, 5, 5, 4, 3.5, 3.5, 3.5, 3],
    },
  ];

  return (
    <div className="w-full h-96 p-4">
      <ReactApexChart
        options={options}
        series={series}
        type="bar"
        height={350}
      />
    </div>
  );
};

export default DataChart;
