import { Box, Typography } from "@mui/material";
import React from "react";
import feaImg from "../Assets/images.jpeg";


const Sajan = () => {
  return (
    <Box
      sx={{
        height: "100vh",
        width: "85vw",
        display: "flex",
        alignItems: "center",
        gap: "1rem ",
        flexDirection: "column",
      }}
    >
      <Box
        sx={{
          p: "1rem",
          display: "flex",
          justifyContent: "center",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        <Box
          sx={{
            height: "3rem",
            borderRadius: "10rem",
            width: "3rem",
            overflow: "hidden",
          }}
        >
          <img
            src={feaImg}
            alt="No Preview"
            style={{
              height: "100%",
              width: "100%",
              objectFit: "contain",
              mixBlendMode: "multiply",
            }}
            onError={(e) => {
              e.onerror = null;
              e.target.src = require("../Assets/images.jpeg");
            }}
          />
        </Box>
        <Typography
          className="text-head"
          variant="body1"
          sx={{
            fontSize: "3rem ",
            fontFamily: "Londrina Sketch",
            fontWeight: 900,
          }}
        >
          KA-CHING CONTEST !
        </Typography>
      </Box>
      <Box
        sx={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          p: "1rem",
        }}
      >
        <Typography
          variant="body1"
          sx={{
            fontSize: "3rem ",
            fontFamily: "Londrina Sketch",
            fontWeight: 900,
            textAlign: "center",
          }}
        >
          Is Your School’s English Curriculum Equipping You For Trending Jobs?
        </Typography>
      </Box>
      {/* <Box
        sx={{ width: "90%", display: "flex", justifyContent: "space-between" }}
      ></Box> */}
    </Box>
  );
};

export default Sajan;
