import { Box, Typography } from "@mui/material";
import React from "react";
import funlearn from "../Assets/funlearn.jpg";

const IntroPage = () => {
  return (
    <>
      <Box>
        <Box
          sx={{
            height: "100vh",
            width: "80vw",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <Box
            sx={{
              p: "2rem",
              borderRadius: "2rem",
              background: "rgba(255, 255, 255, 0.25)",
              boxShadow: "0 8px 32px 0 rgba(31, 38, 135, 0.37)",
              backdropFilter: "blur(4px)",
              WebkitBackdropFilter: "blur(4px)",
              border: "1px solid rgba(255, 255, 255, 0.18)",
              animation: "rgbaShadow 5s ease-in-out infinite",
            }}
          >
            <Typography
              className="text-head"
              variant="body1"
              sx={{
                fontSize: "3rem ",
                fontFamily: "Londrina Sketch",
                fontWeight: 900,
              }}
            >
              Introduction :
            </Typography>
            <Box sx={{ display: "flex", gap: "1rem" }}>
              <Box
                sx={{ display: "flex", gap: "1rem", flexDirection: "column",width:"70%" }}
              >
                <Typography
                  variant="body1"
                  sx={{
                    fontSize: "0.8rem ",
                    fontWeight: 400,
                    fontFamily: "Open Sans, sans-serif",
                  }}
                >
                  In today’s rapidly evolving job market, the demand for skills
                  and competencies is changing faster than ever. With industries
                  being reshaped by technology, digitalization, and
                  globalization, the traditional focus on language and
                  literature in English curricula is being questioned. Are
                  schools adapting to these shifts? Are they preparing students
                  with the communication, critical thinking, and creativity
                  needed for modern careers like content creation, digital
                  marketing, UX writing, and more?
                </Typography>
                <Typography
                  variant="body1"
                  sx={{
                    fontSize: "0.8rem ",
                    fontWeight: 400,
                    fontFamily: "Open Sans, sans-serif",
                  }}
                >
                  Our presentation will explore whether school English curricula
                  are aligned with the demands of trending jobs. We’ll examine
                  the role of English in building essential skills—such as
                  problem-solving, collaboration, storytelling, and effective
                  communication—and discuss areas where the curriculum may need
                  to evolve. Finally, we’ll suggest how schools can enhance
                  English education to better prepare students for opportunities
                  in today’s dynamic job landscape.
                </Typography>
              </Box>
              <Box
                sx={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                }}
              >
                <Box
                  sx={{
                    height: "14rem",
                    borderRadius: "1rem",
                    overflow: "hidden",
                  }}
                >
                  <img
                    src={funlearn}
                    alt="No Preview"
                    style={{
                      height: "100%",
                      width: "100%",
                      objectFit: "contain",
                      mixBlendMode: "multiply",
                    }}
                    onError={(e) => {
                      e.onerror = null;
                      e.target.src = require("../Assets/images.jpeg");
                    }}
                  />
                </Box>
              </Box>
            </Box>
          </Box>
        </Box>
      </Box>
    </>
  );
};

export default IntroPage;
