import { Box, Typography } from "@mui/material";
import React from "react";
import DataChart from "../Component/DataChart";
import PiChart from "../Component/PiChart";

const DataPage = () => {
  return (
    <>
      <Box>
        <Box
          sx={{
            height: "100vh",
            width: "80vw",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <Box
            sx={{
              width: "100%",
              height: "30rem",
              p: "2rem",
              borderRadius: "2rem",
              background: "rgba(255, 255, 255, 0.25)",
              boxShadow: "0 8px 32px 0 rgba(31, 38, 135, 0.37)",
              backdropFilter: "blur(4px)",
              WebkitBackdropFilter: "blur(4px)",
              border: "1px solid rgba(255, 255, 255, 0.18)",
              animation: "rgbaShadow 5s ease-in-out infinite",
            }}
          >
            <Typography
              className="text-head"
              variant="body1"
              sx={{
                fontSize: "3rem ",
                fontFamily: "Londrina Sketch",
                fontWeight: 900,
              }}
            >
              Data :
            </Typography>
            <Box sx={{ display: "flex", justifyContent: "space-around" }}>
              <ul style={{ paddingLeft: "1rem" }}>
                <li>
                  <Typography variant="body1" sx={{ fontSize: "0.8rem" }}>
                    <span style={{ color: "#0080BE", fontWeight: "700" }}>
                      Bar Graph:
                    </span>{" "}
                    It give the data of top ten languages that famous globally.
                  </Typography>
                </li>
              </ul>
              <ul style={{ paddingLeft: "1rem", width: "40%" }}>
                <li>
                  <Typography variant="body1" sx={{ fontSize: "0.8rem" }}>
                    <span style={{ color: "#C04CFF", fontWeight: "700" }}>
                      Pi Chart:
                    </span>{" "}
                    It show the data of tech that most of the website use
                    English language in there website for show there data.
                  </Typography>
                </li>
              </ul>
            </Box>
            <Box>
              <Box sx={{ display: "flex", gap: "2rem" }}>
                <Box
                  sx={{
                    width: "50%",
                    display: "flex",
                    flexDirection: "column",
                    textAlign: "center",
                  }}
                >
                  <DataChart />
                </Box>
                <Box sx={{ width: "50%" }}>
                  <PiChart />
                  <Typography
                    className="text-head"
                    variant="body1"
                    sx={{
                      fontSize: ".8rem ",
                      fontWeight: 500,
                      textAlign:"center"
                    }}
                  >
                    Percentages of websites Using various content Languages
                  </Typography>
                </Box>
              </Box>
            </Box>
          </Box>
        </Box>
      </Box>
    </>
  );
};

export default DataPage;
