import React from "react";
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
} from "@mui/material";

function ComparisonTable() {
  return (
    <TableContainer component={Paper} sx={{ height: "22rem" }}>
      <Table sx={{ minWidth: 650 }} aria-label="comparison table">
        <TableHead>
          <TableRow>
            <TableCell>
              <strong>Aspect</strong>
            </TableCell>
            <TableCell align="center">
              <strong>Traditional English Curriculum</strong>
            </TableCell>
            <TableCell align="center">
              <strong>Modern English Curriculum</strong>
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          <TableRow>
            <TableCell>Focus</TableCell>
            <TableCell align="center">
              Literature, grammar, poetry analysis
            </TableCell>
            <TableCell align="center">
              Practical communication, real-world writing, digital literacy
            </TableCell>
          </TableRow>
          <TableRow>
            <TableCell>Learning Style</TableCell>
            <TableCell align="center">
              Memorization, exams, and essays
            </TableCell>
            <TableCell align="center">
              Hands-on projects, presentations, real-world applications
            </TableCell>
          </TableRow>
          <TableRow>
            <TableCell>Skills Developed</TableCell>
            <TableCell align="center">
              Basic reading and writing skills
            </TableCell>
            <TableCell align="center">
              Critical thinking, problem-solving, collaboration
            </TableCell>
          </TableRow>
          <TableRow>
            <TableCell>Relevance to Jobs</TableCell>
            <TableCell align="center">Limited direct job application</TableCell>
            <TableCell align="center">
              Strong alignment with workplace needs and industry skills
            </TableCell>
          </TableRow>
          <TableRow>
            <TableCell>Tools and Technology</TableCell>
            <TableCell align="center">Minimal use of technology</TableCell>
            <TableCell align="center">
              Integration of digital tools, multimedia, and online communication
            </TableCell>
          </TableRow>
          <TableRow>
            <TableCell>Global Relevance</TableCell>
            <TableCell align="center">
              Focused on regional or national literature and language
            </TableCell>
            <TableCell align="center">
              Prepares students for global careers with cross-cultural
              communication skills
            </TableCell>
          </TableRow>
        </TableBody>
      </Table>
    </TableContainer>
  );
}

export default ComparisonTable;
