import { Box, Typography } from "@mui/material";
import React from "react";
import comunication from "../Assets/communication skills.jpg";
const Communication = () => {
  return (
    <>
      <Box>
        <Box
          sx={{
            height: "100vh",
            width: "80vw",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            flexDirection: "column",
            gap: "1rem",
          }}
        >
          <Box
            sx={{
              width: "100%",
              p: "1rem",
              borderRadius: "2rem",
              background: "rgba(255, 255, 255, 0.25)",
              boxShadow: "0 8px 32px 0 rgba(31, 38, 135, 0.37)",
              backdropFilter: "blur(4px)",
              WebkitBackdropFilter: "blur(4px)",
              border: "1px solid rgba(255, 255, 255, 0.18)",
              animation: "rgbaShadow 5s ease-in-out infinite",
            }}
          >
            <Typography
              className="text-head"
              variant="body1"
              sx={{
                fontSize: "3rem ",
                fontFamily: "Londrina Sketch",
                fontWeight: 900,
              }}
            >
              Communication Skills :
            </Typography>
            <Box sx={{ display: "flex", gap: "1rem" }}>
              <Box
                sx={{
                  width: "60%",
                  display: "flex",
                  gap: "0.5rem",
                  flexDirection: "column",
                }}
              >
                <Typography
                  variant="body1"
                  sx={{
                    fontSize: "0.8rem ",
                    fontWeight: 400,
                    fontFamily: "Open Sans, sans-serif",
                  }}
                >
                  Communication skills are essential for expressing ideas
                  clearly and connecting with others in both personal and
                  professional settings. They help in building relationships,
                  solving problems, collaborating in teams, and reducing
                  misunderstandings. Strong communication abilities are a
                  foundation for success in any career, especially in today’s
                  global and digital world.
                </Typography>
                <Typography
                  variant="body1"
                  sx={{
                    fontSize: "0.8rem ",
                    fontWeight: 400,
                    fontFamily: "Open Sans, sans-serif",
                  }}
                >
                  Through communication, we develop essential skills and
                  important qualities.
                  <ul style={{ paddingLeft: "1rem" }}>
                    <li>
                      <span style={{ color: "#FF4C7A", fontWeight: "700" }}>
                        Verbal Communication:
                      </span>{" "}
                      Expressing ideas effectively through speech in
                      conversations, meetings, or presentations.
                    </li>
                    <li>
                      {" "}
                      <span style={{ color: "#5C9F3C", fontWeight: "700" }}>
                        Non-verbal Communication:
                      </span>{" "}
                      Using gestures, facial expressions, and tone of voice to
                      enhance messages.{" "}
                    </li>
                    <li>
                      <span style={{ color: "#F69C29", fontWeight: "700" }}>
                        {" "}
                        Written Communication:
                      </span>{" "}
                      Crafting clear emails, reports, content, or messages for
                      professional and creative purposes.
                    </li>
                    <li>
                      <span style={{ color: "#FF4C7A", fontWeight: "700" }}>
                        {" "}
                        Persuasive Communication:{" "}
                      </span>{" "}
                      Convincing others through arguments, ideas, or solutions
                      (useful in sales, marketing, and leadership roles)
                    </li>
                    <li>
                      <span style={{ color: "#5C9F3C", fontWeight: "700" }}>
                        {" "}
                        Listening Skills:{" "}
                      </span>
                      Actively understanding what others are saying, which helps
                      in responding thoughtfully.
                    </li>
                    <li>
                      <span style={{ color: "#0080BE", fontWeight: "700" }}>
                        Feedback Skills:{" "}
                      </span>
                      Giving and receiving constructive feedback for
                      improvement.
                    </li>
                  </ul>
                </Typography>
                <Typography
                  variant="body1"
                  sx={{
                    fontSize: "0.8rem ",
                    fontWeight: 400,
                    fontFamily: "Open Sans, sans-serif",
                  }}
                >
                  Importance in the Workplace:
                  <ul style={{ paddingLeft: "1rem" }}>
                    <li>
                      Helps in{" "}
                      <span style={{ color: "#FF4C7A", fontWeight: "700" }}>
                        teamwork
                      </span>{" "}
                      by ensuring clear coordination and collaboration.
                    </li>
                    <li>
                      Essential for
                      <span style={{ color: "#0080BE", fontWeight: "700" }}>
                        {" "}
                        customer service{" "}
                      </span>
                      to solve queries and manage client relationships.
                    </li>
                    <li>
                      Plays a key role in{" "}
                      <span style={{ color: "#F69C29", fontWeight: "700" }}>
                        leadership
                      </span>{" "}
                      and{" "}
                      <span style={{ color: "#5C9F3C", fontWeight: "700" }}>
                        management
                      </span>{" "}
                      by motivating and guiding teams.
                    </li>
                  </ul>
                </Typography>
              </Box>
              <Box
                sx={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                }}
              >
                <Box
                  sx={{
                    height: "14rem",
                    borderRadius: "1rem",
                    overflow: "hidden",
                  }}
                >
                  <img
                    src={comunication}
                    alt="No Preview"
                    style={{
                      height: "100%",
                      width: "100%",
                      objectFit: "contain",
                      mixBlendMode: "multiply",
                    }}
                    onError={(e) => {
                      e.onerror = null;
                      e.target.src = require("../Assets/images.jpeg");
                    }}
                  />
                </Box>
              </Box>
            </Box>
          </Box>
        </Box>
      </Box>
    </>
  );
};

export default Communication;
