import { Box, Typography } from "@mui/material";
import React from "react";
import skills from "../Assets/english.jpg";

const AboutEnglis = () => {
  return (
    <>
      <Box>
        <Box
          sx={{
            height: "100vh",
            width: "80vw",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            flexDirection: "column",
            gap: "1rem",
          }}
        >
          <Box
            sx={{
              width: "100%",
              height: "70%",
              p: "1rem",
              borderRadius: "2rem",
              background: "rgba(255, 255, 255, 0.25)",
              boxShadow: "0 8px 32px 0 rgba(31, 38, 135, 0.37)",
              backdropFilter: "blur(4px)",
              WebkitBackdropFilter: "blur(4px)",
              border: "1px solid rgba(255, 255, 255, 0.18)",
              animation: "rgbaShadow 5s ease-in-out infinite",
            }}
          >
            <Typography
              className="text-head"
              variant="body1"
              sx={{
                fontSize: "3rem ",
                fontFamily: "Londrina Sketch",
                fontWeight: 900,
              }}
            >
              About the English language :
            </Typography>
            <Box sx={{ display: "flex", gap: "1rem" }}>
              <Box
                sx={{
                  width: "60%",
                  display: "flex",
                  gap: "0.5rem",
                  flexDirection: "column",
                }}
              >
                <Typography
                  variant="body1"
                  sx={{
                    fontSize: "0.8rem ",
                    fontWeight: 400,
                    fontFamily: "Open Sans, sans-serif",
                  }}
                >
                  English is one of the most widely spoken languages in the
                  world, serving not only as a medium of communication but also
                  as a key tool in education, business, and technology. With
                  over 1.5 billion speakers globally, it plays a crucial role in
                  international relations, academia, and the job market. In
                  fact, English has become the primary language of many
                  industries, including tech, media, marketing, and science,
                  making it essential for students preparing to enter a
                  competitive workforce.
                </Typography>
                <Typography
                  variant="body1"
                  sx={{
                    fontSize: "0.8rem ",
                    fontWeight: 400,
                    fontFamily: "Open Sans, sans-serif",
                  }}
                >
                  Beyond basic communication, Some key skills developed through
                  learning English include:
                  <ul style={{ paddingLeft: "1rem" }}>
                    <li>
                      <Typography variant="body1" sx={{ fontSize: "0.8rem" }}>
                        <span style={{ color: "#C04CFF", fontWeight: "700" }}>
                          Reading{" "}
                        </span>
                        and{" "}
                        <span style={{ color: "#FF8A66", fontWeight: "700" }}>
                          comprehension skills
                        </span>
                      </Typography>
                    </li>
                    <li>
                      <Typography variant="body1" sx={{ fontSize: "0.8rem" }}>
                        <span style={{ color: "#0080BE", fontWeight: "700" }}>
                          writing{" "}
                        </span>
                        and{" "}
                        <span style={{ color: "#FF4C7A", fontWeight: "700" }}>
                          expression skills
                        </span>
                      </Typography>
                    </li>
                    <li>
                      <Typography variant="body1" sx={{ fontSize: "0.8rem" }}>
                        <span style={{ color: "#5C9F3C", fontWeight: "700" }}>
                          Speaking{" "}
                        </span>
                        and{" "}
                        <span style={{ color: "#06FDFD", fontWeight: "700" }}>
                          Presenting confidently
                        </span>
                      </Typography>
                    </li>
                  </ul>
                </Typography>
                <Typography
                  variant="body1"
                  sx={{
                    fontSize: "0.8rem ",
                    fontWeight: 400,
                    fontFamily: "Open Sans, sans-serif",
                  }}
                >
                  Thus, the question arises: Is Your School’s English Curriculum
                  Equipping You For Trending Jobs?
                </Typography>
              </Box>
              <Box
                sx={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                }}
              >
                <Box
                  sx={{
                    height: "13rem",
                    borderRadius: "1rem",
                    overflow: "hidden",
                  }}
                >
                  <img
                    src={skills}
                    alt="No Preview"
                    style={{
                      height: "100%",
                      width: "100%",
                      objectFit: "contain",
                      mixBlendMode: "multiply",
                    }}
                    onError={(e) => {
                      e.onerror = null;
                      e.target.src = require("../Assets/images.jpeg");
                    }}
                  />
                </Box>
              </Box>
            </Box>
          </Box>
        </Box>
      </Box>
    </>
  );
};

export default AboutEnglis;
